<template>
  <div class="header" @click="backHome">
    <p class="Hometown" @click='myHomeTown'></p>
    <img src="@/assets/img/family.jpg" alt="">
    <div></div>
  </div>
</template>

<script>
  export default {
    methods:{
      backHome(){
        this.$router.push('/pages/home')
      },
      myHomeTown(){
        alert('')
      }
    }
  }
</script>

<style scoped>
.header{
  width:100%;
  height:4rem;
  border-bottom:.1rem solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:#fff(116, 69, 245);
  box-sizing: border-box;
  
}
.header>img{
  width:50%;
  height:90%;
}
.Hometown{
  padding-left:.3rem;
  font-size:1.4rem;
  font-weight: bold;
  color:#000;
}
</style>


